import TimePicker from './src/time-picker.vue';
import TimePickerPanel from './src/time-picker-panel.vue';

/* istanbul ignore next */
TimePicker.install = function (Vue) {
  Vue.component(TimePicker.name, TimePicker);
  Vue.component(TimePickerPanel.name, TimePickerPanel);
};

TimePicker.panel = TimePickerPanel;
export { TimePicker };
export { TimePickerPanel };
